.App {
  font-family: sans-serif;
  text-align: center;
}
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
main {
  width: 100vw;
  min-height: 100vh;
  max-height: max-content;
}

.movie-container {
  max-width: 100vw;
  min-width: 100vw;
  min-height: 100vh;
  max-height: max-content;
  background-color: #2b2b31;
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  justify-content: space-evenly;
  margin-top: 1.5rem;
  align-items: center;
}
